@import "../../../assets/styles/entry.scss";

.item {
  @include text();
  page-break-inside: avoid;
  position: relative;
  &:not(:last-child) {
    margin-bottom: 12px;
  }
  &:not(:first-child) {
    &:before {
      content: "";
      display: block;
      height: 1px;
      width: 100%;
      background-color: var(--text-color);
      transition: background-color $speed;
      opacity: 0.1;
      margin-bottom: 12px;
    }
  }
}

.period {
  @include period();
}
