@import "../../../assets/styles/entry.scss";

.grid {
  display: grid;
  grid-template-columns: 100px auto;
  grid-row-gap: 16px;
  grid-column-gap: 40px;
  margin-top: 20px;
  @include max-vw($sm) {
    display: flex;
    flex-direction: column;
    grid-row-gap: 0;
  }
}

.list {
  li {
    @include li();
  }
}

.item {
  @include text();
  page-break-inside: avoid;
  position: relative;
  &:not(:last-child) {
    margin-bottom: 20px;
  }
  &:not(:first-child) {
    &:before {
      content: "";
      display: block;
      height: 1px;
      width: 100%;
      background-color: var(--text-color);
      transition: background-color $speed;
      opacity: 0.1;
      margin-bottom: 20px;
    }
  }
  pre {
    margin: 0;
    white-space: normal;
    white-space: break-spaces;
    font-family: inherit;
  }
}

.key {
  opacity: 0.6;
  @include max-vw($sm) {
    margin-bottom: 4px;
    &:not(:first-child) {
      margin-top: 20px;
    }
  }
}
