@import "../../../assets/styles/entry.scss";

.stack {
  display: grid;
  grid-template-columns: repeat(auto-fill, 100px);
  grid-column-gap: 16px;
  grid-row-gap: 16px;
  margin-top: 16px;
  @include max-vw($md) {
    grid-template-columns: repeat(auto-fill, 98px);
  }

  @media print {
    grid-column-gap: 14px;
    grid-row-gap: 14px;
    margin-top: 14px;
    grid-template-columns: repeat(auto-fill, 90px);
  }
}

.block {
  display: flex;
  flex-direction: column;
  align-items: center;
  img {
    border-radius: 4px;
    width: 60px;
    height: 60px;
    @media print {
      width: 40px;
      height: 40px;
    }
  }
  span {
    margin-top: 8px;
    display: block;
    font-size: 15px;
    @include max-vw($md) {
      font-size: 14px;
    }
  }
}
